import React from 'react';
import header from '../images/badge-header.svg';
import Badge from '../components/Badge'; //props
import BadgeForm from '../components/BadgeForm'
import './styles/BadgeNew.css'

class BadgeNew extends React.Component{
    state = { form:{
        firstName: '',
        lastName: '',
        email: '',
        jobTitle: '',
        twitter: '',
    }};
    handleChange = (e) =>{
        this.setState({
            form: {
                ...this.state.form,
                [e.target.name] : e.target.value,
            }
        })
    }
    render(){
        return(
            <React.Fragment>
                <div className='BadgeNew__hero'>
                    <img className='img-fluid' src={header} alt=""/>
                </div>
                <div className='container'>
                    <div className='row'>
                        <div className='col'>
                        <Badge firstName={this.state.form.firstName} lastName={this.state.form.lastName} email={this.state.form.email} avatar='https://www.gravatar.com/avatar/205e460b479e2e5b48aec07710c08d50' jobTitle={this.state.form.jobTitle} twitter={this.state.form.twitter}/>
                        </div>
                        <div className='col-6'>
                        <BadgeForm onChange={this.handleChange} formValues={this.state.form}/>
                        </div>
                    </div>
                </div>

            </React.Fragment>
        );
    }
}
export default BadgeNew;